<template>
  <div>
    <md-table
      table-header-color="red"
      v-model="searched"
      md-sort="name"
      md-sort-order="asc"
      md-card
    >
      <md-table-toolbar>
        <!-- Sp xp theo danh sch n do mnh to hay danh sch n do ngi khc gi -->
        <md-field class="md-toolbar-section-start">
          <md-select
            v-model="typeDebtReminder"
            name="typeDebtReminder"
            id="typeDebtReminder"
            placeholder="From"
            @md-selected="changeType($event)"
          >
            <md-option value="from">From</md-option>
            <md-option value="to">To</md-option>
          </md-select>
        </md-field>
        <!-- Thanh tm kim -->
        <md-field md-clearable class="md-toolbar-section-end">
          <md-input
            placeholder="Tm kim theo STK..."
            v-model="search"
            @input="searchOnTable"
          />
        </md-field>
      </md-table-toolbar>
      <!-- Hin th Not Found khi khng tm c -->
      <md-table-empty-state
        md-label="Khng tm thy danh sch n"
        :md-description="
          `Khng tm thy danh sch n cho tm kim '${search}'.`
        "
      >
      </md-table-empty-state>

      <!-- D liu c hin th  y nu c -->
      <md-table-row slot="md-table-row" slot-scope="{ item }">
        <md-table-cell md-label="M nhc n" md-sort-by="MaGiaoDichNhacNo">{{
          item.MaGiaoDichNhacNo
        }}</md-table-cell>
        <md-table-cell md-label="STK" md-sort-by="SoTaiKhoanNguoiNhan"
          >{{ item.SoTaiKhoanNguoiNhan }}
        </md-table-cell>
        <md-table-cell md-label="H tn" md-sort-by="SoTaiKhoanNguoiNhan"
          >{{ item.TenKhachHang }}
        </md-table-cell>
        <md-table-cell md-label="Thi gian" md-sort-by="NgayGiaoDich">{{
          item.NgayGiaoDich
        }}</md-table-cell>
        <md-table-cell md-label="Ni dung" md-sort-by="NoiDung">{{
          item.NoiDung
        }}</md-table-cell>
        <md-table-cell md-label="S tin" md-sort-by="SoTien">{{
          new Intl.NumberFormat("it-IT", {
            style: "currency",
            currency: "VND"
          }).format(item.SoTien)
        }}</md-table-cell>
        <md-table-cell>
          <md-button class="md-icon-button md-primary">
            <md-icon>edit</md-icon>
          </md-button>
          <md-button
            class="md-icon-button md-primary md-raised"
            @click="remove(item)"
          >
            <md-icon>delete</md-icon>
          </md-button>
        </md-table-cell>
      </md-table-row>
    </md-table>
    <md-dialog-confirm
      :md-active.sync="activeRemoveDR"
      md-title="Hy danh sch nhc n"
      md-content="Bn c chc chn mun hy nhc n ny? <br>
      Chng ti s gi mt thng bo ti ti khon ch  xc nhn thng tin ca bn."
      md-confirm-text="ng "
      md-cancel-text="Hy"
      @md-cancel="onCancel"
      @md-confirm="onConfirm"
    />
    <md-dialog-prompt
      :md-active.sync="valueDialog"
      v-model="valueMessage"
      md-title="Vui lng nhp ni dung s gi n ti khon nhc n."
      md-input-maxlength="30"
      md-input-placeholder="Ni dung"
      md-confirm-text="Hon thnh"
      md-cancel-text="Hy"
      @md-confirm="onConfirmToDB"
    />
  </div>
</template>

<script>
import { mapGetters, mapState, mapActions } from "vuex";
// import moment from "moment";

const toLower = text => {
  return text.toString().toLowerCase();
};

const searchByName = (items, term) => {
  if (term) {
    return items.filter(item => toLower(item.NoiDung).includes(toLower(term)));
  }
  return items;
};
export default {
  name: "debt-reminder-table",
  props: ["debtReminder"],
  data: () => ({
    // Tm kim bng Search
    search: null,
    searched: [],
    // tm kim theo ngun hay ch
    typeDebtReminder: "from",
    //Hy giao dich
    isRemove: false,
    //Active dialog hy nhc n
    activeRemoveDR: false,
    valueDialog: null,
    valueMessage: null,
    ConfirmToDB: false,
    selectedRemoveItem: null
  }),
  methods: {
    //Dialog hy nhc n
    onConfirm() {
      this.valueDialog = true;
    },
    onCancel() {
      this.valueDialog = false;
    },
    onConfirmToDB() {
      this.ConfirmToDB = true;
      if (this.ConfirmToDB) {
        console.log("HERE: Remove Item Function", item.idGiaoDichNhacNo);
        const { valueMessage } = this.valueMessage;
        const { selectedRemoveItem } = this.selectedRemoveItem;
        this.removeDebtReminder({ item, valueMessage });
      }
    },
    ...mapActions(["removeDebtReminder"]),
    searchOnTable() {
      this.typeDebtReminder === "from"
        ? (this.searched = searchByName(this.debtReminder.source, this.search))
        : (this.searched = searchByName(
            this.debtReminder.destination,
            this.search
          ));
    },
    changeType(event) {
      this.typeDebtReminder === "from"
        ? (this.searched = this.debtReminder.source)
        : (this.searched = this.debtReminder.destination);
    },
    remove(item) {
      //Actibe form hy nhc n
      this.activeRemoveDR = true;
      this.isRemove = true;
      this.selectedRemoveItem = item.idGiaoDichNhacNo;
      //console.log("HERE: Remove Item Function", item.idGiaoDichNhacNo);
      // if (this.ConfirmToDB) {
      //   console.log("HERE: Remove Item Function", item.idGiaoDichNhacNo);
      //   const { valueMessage } = this.valueMessage;
      //   this.removeDebtReminder({ item, valueMessage });
      // }
    }
  },
  created() {
    console.log("Table Created: ", this.debtReminder);
    this.debtReminder ? (this.searched = this.debtReminder.source) : null;
  },
  watch: {
    debtReminder: function(val) {
      //console.log("Val: ", val);
      this.searched = val.source;
    }
  }
};
</script>
